<template>
  <div class="achievement">
    <div class="ach_eval">
      <div class="left">
        <div class="department_ach table_box">
          <table>
            <thead>
              <tr>
                <th colspan="2" style="width:16vw">部门绩效</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>部门</td>
                <td>{{ dpmAch.length > 0 ? dpmAch[0].department : "" }}</td>
              </tr>
              <tr>
                <td>绩效评定时间</td>
                <td>{{ dpmAch.length > 0 ? dpmAch[0].evaluate_date : "" }}</td>
              </tr>
              <tr>
                <td>当月绩效最高标准</td>
                <td>
                  {{ dpmAch.length > 0 ? dpmAch[0].month_top_performance : "" }}
                </td>
              </tr>
              <tr>
                <td>当月最高绩效总额</td>
                <td>
                  {{ dpmAch.length > 0 ? dpmAch[0].month_top_all_money : "" }}
                </td>
              </tr>
              <tr>
                <td>当月实际绩效标准</td>
                <td>
                  {{
                    dpmAch.length > 0 ? dpmAch[0].month_actual_performance : ""
                  }}
                </td>
              </tr>
              <tr>
                <td>当月实际绩效总额</td>
                <td>
                  {{
                    dpmAch.length > 0 ? dpmAch[0].month_actual_all_money : ""
                  }}
                </td>
              </tr>
              <tr>
                <td>当月部门主管绩效标准</td>
                <td>
                  {{
                    dpmAch.length > 0
                      ? dpmAch[0].leader_performance_standard
                      : ""
                  }}
                </td>
              </tr>
              <tr>
                <td>部门主管绩效基数</td>
                <td>
                  {{
                    dpmAch.length > 0 ? dpmAch[0].leader_performance_base : ""
                  }}
                </td>
              </tr>
              <tr>
                <td>部门主管绩效</td>
                <td>
                  {{ dpmAch.length > 0 ? dpmAch[0].leader_performance : "" }}
                </td>
              </tr>
              <tr>
                <td>其他员工绩效</td>
                <td>
                  {{ dpmAch.length > 0 ? dpmAch[0].staff_all_performance : "" }}
                </td>
              </tr>
              <tr>
                <td>备注</td>
                <td>{{ dpmAch.length > 0 ? dpmAch[0].remarks : "" }}</td>
              </tr>
            </tbody>
          </table>
        </div>
        <div class="dpm_history">
          <el-button type="primary" size="mini" @click="dpmHistory"
            >查看历史部门绩效</el-button
          >
        </div>
      </div>
      <div class="right">
        <div class="emp_ach table_box">
          <table>
            <thead>
              <tr>
                <th colspan="12">员工绩效</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>序号</td>
                <td>员工编码</td>
                <td>员工姓名</td>
                <td>职位</td>
                <td>基本工资</td>
                <td>入职时间</td>
                <td>绩效基数</td>
                <td>当月绩效最高标准</td>
                <td>当月最高绩效金额</td>
                <td>当月实际绩效标准</td>
                <td>当月实际绩效金额</td>
                <td>评价</td>
              </tr>
              <tr v-for="(item, index) in empAch" :key="index">
                <td>{{ index + 1 }}</td>
                <td>{{ item.personnel_id }}</td>
                <td>{{ item.user_name }}</td>
                <td>{{ item.position }}</td>
                <td>{{ item.base_pay }}</td>
                <td>{{ item.entry_date }}</td>
                <td>{{ item.performance_base }}</td>
                <td>{{ item.month_top_performance }}</td>
                <td>{{ item.month_top_money }}</td>
                <!-- 可编辑 -->
                <td v-if="isEdit == false">
                  {{ item.month_actual_performance }}
                </td>
                <td v-else>
                  <el-input
                    v-model="editForm[index].month_actual_performance"
                  ></el-input>
                </td>
                <td v-if="isEdit == false">{{ item.month_actual_money }}</td>
                <td v-else>
                  <el-input
                    v-model="editForm[index].month_actual_money"
                  ></el-input>
                </td>
                <td v-if="isEdit == false">{{ item.evaluate }}</td>
                <td v-else>
                  <el-input v-model="editForm[index].evaluate"></el-input>
                </td>
              </tr>
            </tbody>
          </table>
          <div class="visit_history">
            <el-button type="primary" size="mini" @click="visitHistoryAch"
              >查看历史员工绩效</el-button
            >
          </div>
          <div class="operate">
            <el-button type="primary" @click="submit">提交</el-button>
            <el-button type="primary" @click="editEmpAch" v-if="isEdit == false"
              >编辑</el-button
            >
            <el-button type="primary" @click="noEmpAch" v-else>取消</el-button>
            <el-button type="primary" @click="baocun">保存</el-button>
          </div>
        </div>
      </div>
    </div>

    <!-- 查看历史员工绩效 -->
    <el-dialog
      @close="deleteList"
      class="person_history"
      title="员工历史绩效"
      :visible.sync="historyDialog.dialogVisible"
      width="80%"
      center
    >
      <div class="table_box">
        <table>
          <thead>
            <tr>
              <th width="299px">绩效评定时间</th>
              <th>
                <el-dropdown @command="changeStaff" trigger="click">
                  <span class="el-dropdown-link">
                    员工姓名
                    <i class="el-icon-arrow-down el-icon--right"></i>
                  </span>
                  <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item
                      v-for="(item, index) in historyStaffList"
                      :key="index"
                      :command="item.id"
                      >{{ item.username }}</el-dropdown-item
                    >
                  </el-dropdown-menu>
                </el-dropdown>
              </th>
              <th>职位</th>
              <th>基本工资</th>
              <th>绩效基数</th>
              <th>当月绩效最高标准</th>
              <th>当月最高绩效金额</th>
              <th>当月实际绩效标准</th>
              <th>当月实际绩效金额</th>
              <th>评价</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item, index) in selectedStaffHistory" :key="index">
              <td>{{ item.evaluate_date }}</td>
              <td>{{ item.user_name }}</td>
              <td>{{ item.position }}</td>
              <td>{{ item.base_pay }}</td>
              <td>{{ item.performance_base }}</td>
              <td>{{ item.month_top_performance }}</td>
              <td>{{ item.month_top_money }}</td>
              <td>{{ item.month_actual_performance }}</td>
              <td>{{ item.month_actual_money }}</td>
              <td>{{ item.evaluate }}</td>
            </tr>
          </tbody>
        </table>
        <!-- <el-pagination
        @size-change="empHandleSizeChange"
        @current-change="empHandleCurrentChange"
        :current-page="historyDialog.currentPage"
        :page-sizes="[4, 6, 8, 10]"
        :page-size="historyDialog.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="historyDialog.total"
      ></el-pagination> -->
      </div>
    </el-dialog>

    <!-- 查看部门历史绩效 -->
    <el-dialog
      class="history"
      :title="dpmAch + '部门历史绩效'"
      :visible.sync="dpmHistoryDialog.dialogVisible"
      width="80%"
      center
    >
      <div class="table_box">
        <table>
          <thead>
            <tr>
              <th>序号</th>
              <th>绩效评定时间</th>
              <th>当月绩效最高标准</th>
              <th>当月最高绩效总额</th>
              <th>当月实际绩效标准</th>
              <th>当月实际绩效总额</th>
              <th>当月部门主管绩效标准</th>
              <th>部门主管绩效基数</th>
              <th>部门主管绩效</th>
              <th>其它员工绩效</th>
              <th>备注</th>
            </tr>
          </thead>
          <tbody>
            <tr
              v-for="(item, index) in dpmHistoryDialog.dpmHistoryList"
              :key="index"
            >
              <td>{{ index + 1 }}</td>
              <td>{{ item.evaluate_date }}</td>
              <td>{{ item.month_top_performance }}</td>
              <td>{{ item.month_top_all_money }}</td>
              <td>{{ item.month_actual_performance }}</td>
              <td>{{ item.month_actual_all_money }}</td>
              <td>{{ item.leader_performance_standard }}</td>
              <td>{{ item.leader_performance_base }}</td>
              <td>{{ item.leader_performance }}</td>
              <td>{{ item.staff_all_performance }}</td>
              <td>{{ item.remarks }}</td>
            </tr>
          </tbody>
          <!-- <el-pagination
           @size-change="dpmHandleSizeChange"
           @current-change="dpmHandleCurrentChange"
           :current-page="dpmHistoryDialog.currentPage"
           :page-sizes="[4, 6, 8, 10]"
           :page-size="dpmHistoryDialog.pageSize"
           layout="total, sizes, prev, pager, next, jumper"
           :total="dpmHistoryDialog.total"
         ></el-pagination> -->
        </table>
      </div>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: "",
  props: {},
  data() {
    return {
      //历史员工列表
      historyStaffList: [],
      // 筛选的员工
      selectedStaffHistory: [],
      // 员工绩效列表
      empAch: [],
      // 部门绩效列表
      dpmAch: [],
      // 是否启用编辑
      isEdit: false,
      // 历史员工绩效对话框所需数据
      historyDialog: {
        dialogVisible: false,
        currentPage: 1,
        pageSize: 4,
        total: 0,
        staffHistoryList: []
      },
      // 部门历史绩效对话框所需数据
      dpmHistoryDialog: {
        dialogVisible: false,
        currentPage: 1,
        pageSize: 4,
        total: 0,
        dpmHistoryList: []
      },
      // 编辑员工数据
      editForm: []
    };
  },
  computed: {},
  watch: {},
  methods: {
    // 获取数据
    visitAchScore() {
      this.$api2.visitAchScore().then(res => {
        console.log(res);
        this.empAch = res.data.data;
        this.dpmAch = res.data.department;
      });
    },
    // 点击编辑
    editEmpAch() {
      this.editForm = [];
      for (let item of this.empAch) {
        let obj = {};
        obj.personnel_name = item.user_name;
        obj.month_actual_performance = item.month_actual_performance;
        obj.month_actual_money = item.month_actual_money;
        obj.evaluate = item.evaluate;
        this.editForm.push(obj);
      }
      this.isEdit = true;
    },
    // 取消编辑
    noEmpAch() {
      this.isEdit = false;
    },
    // 保存
    baocun() {
      this.$confirm("是否确认保存员工数据", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          let formData = new FormData();
          formData.append(
            "staff_performance_list",
            JSON.stringify(this.editForm)
          );
          this.$api2.baocun(formData).then(res => {
            console.log(res);
            if (res) {
              this.isEdit = false;
              this.visitAchScore();
              this.$message({
                type: "success",
                message: "保存成功!"
              });
            }
          });
        })
        .catch(() => {
          // 取消
          this.noEmpAch();
          this.$message({
            type: "info",
            message: "已取消保存"
          });
        });
    },
    submit() {
      this.$confirm("是否确认提交员工数据", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          let formData = new FormData();
          formData.append(
            "staff_performance_list",
            JSON.stringify(this.editForm)
          );
          this.$api2.submit(formData).then(res => {
            console.log(res);
            if (res) {
              this.isEdit = false;
              this.visitAchScore();
              this.$message({
                type: "success",
                message: "提交成功!"
              });
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消保存"
          });
        });
    },
    changeStaff(id) {
      console.log(id);
      console.log(this.historyDialog.staffHistoryList);
      this.selectedStaffHistory = this.historyDialog.staffHistoryList.filter(
        i => i.id == id
      );
    },
    deleteList() {
      this.historyStaffList = [];
    },
    // 查看员工历史绩效
    visitHistoryAch() {
      this.$api2.staffHistoryAch().then(res => {
        console.log(res);
        this.historyDialog.staffHistoryList = res.data.data;
        this.selectedStaffHistory = res.data.data;
        // this.historyStaffList
        res.data.data.forEach(i => {
          let obj = {};
          obj.username = i.user_name;
          obj.id = i.id;
          this.historyStaffList.push(obj);
        });
      });
      this.historyDialog.dialogVisible = true;
    },
    // 查看部门历史绩效
    dpmHistory() {
      this.$api2.dpmHistoryAch().then(res => {
        console.log(res);
        this.dpmHistoryDialog.dpmHistoryList = res.data.data;
      });
      this.dpmHistoryDialog.dialogVisible = true;
    },
    /*
     * 分页相关
     */
    //部门绩效分页
    // 每页显示条数
    dpmHandleSizeChange(val) {
      console.log(val);
    },
    // 当前页
    dpmHandleCurrentChange(val) {
      console.log(val);
    },
    // 员工绩效分页
    empHandleSizeChange(val) {
      console.log(val);
    },
    empHandleCurrentChange(val) {
      console.log(val);
    }
  },
  created() {
    this.visitAchScore();
  },
  mounted() {}
};
</script>
<style scoped>
/* @import url("/static/css/thead_fixed_table.css"); */
@import "../../../static/css/thead_fixed_table.css";

.ach_eval {
  text-align: left;
  /* display: flex; */
  margin-top: 60px;
  /* height: 70vh; */
}
.left {
  float: left;
  width: 30%;
  border-right: 1px solid black;
}
.right {
  width: 70%;
  float: right;
}
.emp_ach {
  position: relative;
  /* width: 80%; */
  /* margin: 0 auto; */
}
.person_history .table_box {
  width: 71vw;
}
.operate {
  width: fit-content;
  margin: 0 auto;
  margin-top: 24px;
}
.history .table_box {
  width: 68vw;
}
.person_history tbody td {
  width: 7vw;
}
.person_history thead th {
  width: 7vw;
}
.history thead th {
  width: 6vw;
}
.visit_history {
  position: absolute;
  top: 17px;
  left: 30px;
}
.el-dropdown-link {
  cursor: pointer;
  color: #000;
  font-size: 16px;
  font-weight: bold;
}
.el-icon-arrow-down {
  font-size: 16px;
}
.dpm_history {
  width: fit-content;
  margin: 0 auto;
  margin-top: 24px;
}
.el-dialog__body th {
  padding: 8px 15px;
  line-height: 30px;
}
thead,
tbody tr {
  /* display: table-row-group;
    table-layout: auto; */
}
.emp_ach td {
  padding: 2px 8px;
  line-height: 30px;
}
.emp_ach td {
  padding: 2px 8px;
  line-height: 30px;
}
.department_ach.table_box {
  width: 18vw;
  margin: 0 auto;
}
.department_ach.table_box td {
  padding: 5px 10px;
  line-height: 25px;
}
.department_ach thead th,
.department_ach tbody td {
  width: 8vw;
}
.emp_ach.table_box {
  width: 62vw;
}
.emp_ach tbody td {
  width: 5vw;
}
.emp_ach thead th {
  width: 60vw;
}
table {
  word-break: break-all;
}
</style>
<style>
.achievement .el-dialog__title {
  font-weight: bold;
}
.achievement .el-dialog__header {
  padding: 35px 0;
  padding-bottom: 20px;
}
.achievement .el-dialog--center .el-dialog__body {
  padding-top: 0;
}
.achievement {
  text-align: center;
}
</style>



